class GameView {
  constructor(game, ctx) {
    this.ctx = ctx;
    this.game = game;
    this.climber = this.game.climber;
    this.resetControl();
  }

  setControl(e) {
    switch(e.keyCode) {
      case 37:
        this.controls.left = true;
        break;
      case 39:
        this.controls.right = true;
        break;
      case 38:
        this.controls.jump = true;
        break;
    }
  }

  resetControl() {
    this.controls = {
      left: false,
      right: false,
      jump: false
    };
  }



  bindKeyHandlers() {
    window.addEventListener("keydown", this.setControl);
    window.addEventListener("keyup", this.resetControl);
  }

  start() {
    this.bindKeyHandlers();
    requestAnimationFrame(this.render.bind(this));
  }

  render() {
    const dir = Object.keys(this.controls).filter(key => this.controls[key])[0].toUpperCase();
    if (dir) {
      this.climber.move(dir);
    }
    this.game.draw(this.ctx);
    requestAnimationFrame(this.render.bind(this));
  }
}

export default GameView;
